import React, { useState, useEffect } from "react";
import { useAuth } from "../../contexts/AuthContext";
import { useNavigate, useLocation } from "react-router-dom";
import { Formik, Form, ErrorMessage } from "formik";
import * as Yup from "yup";
import Button from "../../components/common/Button";
import InputField from "../../components/common/InputField";
import Toast from "../../components/common/Toast";
import loginBg from "../../assets/images/authPage.jpg";
import { FcGoogle } from "react-icons/fc";
import { FaFacebookF } from "react-icons/fa";
import { BsApple } from "react-icons/bs";
import axios from "axios";

const API_BASE_URL =
  process.env.REACT_APP_API_BASE_URL || "http://localhost:5000/api";

const LoginPage = () => {
  const { login } = useAuth();
  const navigate = useNavigate();
  const location = useLocation();
  const [toast, setToast] = useState(null);
  const [loading, setLoading] = useState(false);

  const initialValues = { email: "", password: "", role: "shipper" };

  const validationSchema = Yup.object({
    email: Yup.string().email("Invalid email").required("Required"),
    password: Yup.string().required("Required"),
    role: Yup.string().oneOf(["shipper", "customer"]).required("Required"),
  });

  // Handle OAuth errors from query params
  useEffect(() => {
    const params = new URLSearchParams(location.search);
    const error = params.get("error");
    if (error) {
      setToast({ message: decodeURIComponent(error), type: "error" });
    }
  }, [location.search]);

  const handleLogin = async (values, { setSubmitting }) => {
    setLoading(true);
    try {
      const { data } = await axios.post(`${API_BASE_URL}/auth/login`, values);

      if (data.success) {
        login(data.data, data.data.token, 3600);
        setToast({ message: "Logged in successfully!", type: "info" });
        navigate(
          data.data.role === "shipper"
            ? "/shipper/dashboard"
            : "/customer/dashboard"
        );
      } else {
        setToast({ message: data.message || "Login failed", type: "error" });
      }
    } catch (err) {
      console.error(err);
      setToast({
        message: err.response?.data?.errors?.[0] || "Login error",
        type: "error",
      });
    } finally {
      setSubmitting(false);
      setLoading(false);
    }
  };

  // ---------------- OAuth Login ----------------
  const handleOAuthRedirect = async (provider, role) => {
    try {
      setLoading(true);

      // Open OAuth popup (or redirect)
      const response = await axios.post(`${API_BASE_URL}/auth/oauth`, {
        provider,
        role,
      });

      if (response.data.success) {
        const user = response.data.data;
        if (!user.isActive) {
          setToast({ message: "Account is blocked", type: "error" });
          return;
        }
        login(user, user.token, 3600);
        navigate(
          user.role === "shipper" ? "/shipper/dashboard" : "/customer/dashboard"
        );
      } else {
        setToast({
          message: response.data.message || "User does not exist",
          type: "error",
        });
      }
    } catch (err) {
      console.error(err);
      setToast({
        message:
          err.response?.data?.errors?.[0] ||
          "OAuth login failed. User may not exist.",
        type: "error",
      });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div
      className="min-h-screen flex items-center justify-center bg-cover bg-center font-montserrat p-4"
      style={{ backgroundImage: `url(${loginBg})` }}
    >
      <div className="flex flex-col md:flex-row items-center justify-center w-full max-w-6xl gap-20">
        <div className="flex items-center justify-center w-full md:w-[450px] h-[150px]">
          <svg width="120" height="120" viewBox="0 0 64 64" fill="none">
            <circle cx="32" cy="32" r="32" fill="#E5E7EB" />
            <path d="M32 16L40 32H24L32 16Z" fill="#1E40AF" />
            <circle cx="32" cy="42" r="4" fill="#1E40AF" />
          </svg>
        </div>

        <div className="bg-white/90 backdrop-blur-sm rounded-lg p-6 md:p-8 shadow-md flex flex-col justify-center w-full max-w-sm gap-4">
          <h1 className="text-xl sm:text-2xl font-semibold text-start text-gray-800">
            Welcome Back
          </h1>
          <p className="text-xs text-gray-600">
            Don't have an account?{" "}
            <span
              className="text-[#BF9B53] font-medium cursor-pointer px-2 py-1 rounded hover:bg-[#bf9b5360] hover:text-black"
              onClick={() => navigate("/signup")}
            >
              Create an account
            </span>
          </p>

          <Formik
            initialValues={initialValues}
            validationSchema={validationSchema}
            onSubmit={handleLogin}
          >
            {({ values, setFieldValue, isValid, dirty, isSubmitting }) => (
              <Form className="flex flex-col gap-3">
                <InputField
                  label="Email"
                  type="email"
                  placeholder="Enter your email"
                  value={values.email}
                  onChange={(e) => setFieldValue("email", e.target.value)}
                />
                <ErrorMessage
                  name="email"
                  component="div"
                  className="text-xs text-red-500"
                />

                <InputField
                  label="Password"
                  type="password"
                  placeholder="Enter your password"
                  value={values.password}
                  onChange={(e) => setFieldValue("password", e.target.value)}
                />
                <ErrorMessage
                  name="password"
                  component="div"
                  className="text-xs text-red-500"
                />

                <div className="flex gap-2">
                  {["shipper", "customer"].map((r) => (
                    <button
                      key={r}
                      type="button"
                      className={`flex-1 py-1 text-xs font-medium rounded ${
                        values.role === r
                          ? "bg-[#BF9B53] text-white"
                          : "bg-gray-200 text-gray-700"
                      }`}
                      onClick={() => setFieldValue("role", r)}
                    >
                      {r.charAt(0).toUpperCase() + r.slice(1)}
                    </button>
                  ))}
                </div>
                <ErrorMessage
                  name="role"
                  component="div"
                  className="text-xs text-red-500"
                />

                <div className="flex justify-end">
                  <Button
                    type="submit"
                    disabled={!(isValid && dirty) || isSubmitting || loading}
                    className={`px-4 py-1.5 text-xs rounded-md ${
                      isValid && dirty
                        ? "bg-[#BF9B53] text-white hover:bg-[#a6813f]"
                        : "bg-gray-300 text-gray-500 cursor-not-allowed"
                    }`}
                  >
                    {loading ? "Logging in..." : "Login"}
                  </Button>
                </div>

                <div className="flex flex-col gap-2 mt-3">
                  <Button
                    className="w-full flex items-center justify-center bg-black text-white hover:bg-gray-900 gap-2 rounded-full text-xs py-1.5"
                    onClick={() => handleOAuthRedirect("apple", values.role)}
                  >
                    <BsApple size={14} /> Continue with Apple
                  </Button>
                  <Button
                    className="w-full flex items-center justify-center border border-gray-300 bg-white text-gray-700 hover:bg-gray-100 gap-2 rounded-full text-xs py-1.5"
                    onClick={() => handleOAuthRedirect("google", values.role)}
                  >
                    <FcGoogle size={16} /> Continue with Google
                  </Button>
                  <Button
                    className="w-full flex items-center justify-center bg-[#1877F2] text-white hover:bg-[#155db2] gap-2 rounded-full text-xs py-1.5"
                    onClick={() => handleOAuthRedirect("facebook", values.role)}
                  >
                    <FaFacebookF size={14} color="#fff" /> Continue with
                    Facebook
                  </Button>
                </div>
              </Form>
            )}
          </Formik>
        </div>
      </div>

      {toast && (
        <Toast
          message={toast.message}
          type={toast.type}
          onClose={() => setToast(null)}
        />
      )}
    </div>
  );
};

export default LoginPage;
